@media screen and (min-width: 820px) {
  .input-width-grid {
    grid-template-columns: repeat(4, minmax(50px, 218px));
  }

  .input-width-grid3 {
    grid-template-columns: repeat(3, minmax(50px, 218px));
  }

  .input-width-grid4 {
    grid-template-columns: repeat(4, minmax(50px, 218px));
  }
}

.signup-grid {
  display: grid;
  grid-auto-columns: 35% 65%;
}
